.container{
    position:fixed;
    top:0px;
    left:0px;
    width:80%;
    height:80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: rgb(58, 61, 65);
    border: 1px solid white;
    border-radius: 10px;
    transform: translateX(12%) translateY(10%);
    z-index: 10;
    /* transform: translateY(50%); */
}
.container_light{
    position:fixed;
    top:0px;
    left:0px;
    width:80%;
    height:80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: rgb(231, 235, 240);
    border: 1px solid rgb(204, 188, 188);
    color:rgb(95, 149, 243);
    border-radius: 10px;
    transform: translateX(12%) translateY(10%);
    z-index: 10;
}

.backdrop{
    background: rgba(51, 50, 49, 0.747);
    position: fixed;
    top:0px;
    left:0px;
    height: 100vh;
    width:100vw;
    z-index: 5;
}

.cancel_btn{
 color:rgb(245, 239, 239);   
 background-color: rgb(241, 75, 24);
 border: 1px solid white;
border-radius: 10px;
margin-bottom: 10px;
}

.footer{
    /* position: absolute; */
    bottom:0px;
    text-align:center
}

.header{
    border-radius: 10px;
    border: 1px solid black;
    text-align: center;
}
.header_h2{
    padding:0px;
    margin: 0px;
    border-radius: 10px;
}

.body{
    text-align: center;
}

@media only screen and (max-width:800px){
    .container, .container_light{
        width:90%;
        height:90%;
        transform: translateX(5%) translateY(5%);
    }
}