.footer{
    position:fixed;
    bottom: 0px;
    background:rgb(34, 35, 36);
    width:100%;
    color:white;
    display:flex;
    justify-content: center;
    max-height: 50px;
}

.footer_light{
    position:fixed;
    bottom: 0px;
    background:rgb(228, 226, 226);
    width:100%;
    color:rgb(27, 28, 29);
    display:flex;
    justify-content: center;
    max-height: 50px;
}

.container{
    width:100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-end;
}