.news_container{
    height:60%;
    overflow-y: auto;
    padding: 1rem;
    margin-bottom: 1rem;
    max-width:100%;
    background: rgba(63, 61, 61, 0.966);
    color:aliceblue;
}
.news_container_light{
    height:60%;
    overflow-y: auto;
    padding: 1rem;
    padding-bottom: 3rem;
    max-width:100%;
}

@media only screen and (max-width: 600px) {
    .news_container{
        height:60%;
        overflow-y: auto;
        padding: 1rem;
        margin-bottom: 1rem;
        max-width:100%;
        background: rgba(63, 61, 61, 0.966);
        color:aliceblue;
    }
    .news_container_light{
        height:60%;
        overflow-y: auto;
        padding: 1rem;
        padding-bottom: 3rem;
        max-width:100%;
    }
}