.container{
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: auto;
    /* height:80vh; */
    /* border: 1px solid rgb(70, 68, 68); */
    margin-top: 10px;
    border-radius: 20px;
    box-shadow: 5px 5px 10px 5px rgb(119, 118, 118);
    /* margin-bottom: 10%; */
    justify-content: center;
    align-items: center;
    background: rgba(59, 51, 51, 0.966);
    color: antiquewhite;
    /* overflow-y: scroll; */
}
.container_light{
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: auto;
    /* border: 1px solid rgb(70, 68, 68); */
    margin-top: 10px;
    border-radius: 20px;
    box-shadow: 5px 5px 10px 5px rgb(119, 118, 118);
    /* margin-bottom: 10%; */
    justify-content: center;
    align-items: center;

}

.form{
    /* padding-bottom: 20px; */
    display: grid;
    position: relative;
    grid-template-columns: 1.5fr 2fr;
    /* grid-template-columns: ".label .field .err_banner"; */
    padding: 20px;
    padding-bottom: 0px;
    grid-row-gap: 10px;
}

.label{
    color:rgb(19, 233, 222);
}

.label_light{
    color:rgb(0, 102, 255);
}
.field{
    /* position: relative; */
    border:2px solid rgb(171, 198, 248);
    border-radius: 3px;
    margin-bottom: 12px;
}

.button{
    position: relative;
    background: linear-gradient(to right,rgb(83, 191, 241),rgb(4, 92, 255));
    border-radius: 5px;
    width:50%;
    border:0px;
    padding: 6px;
    color:white;
    font-weight: 600;
    cursor: pointer;
    margin-bottom: 20px;
    text-align: center;
}
.err{
    background: rgb(253, 191, 191);
    color: rgb(252, 82, 82);
}

.form_err{
    position: absolute;
    color: rgb(250, 81, 81);
    margin-bottom: 5px;
    /* top:-20px; */
    right:40px;
    top: 0px;
    font-size: 12px;
}

.err_banner{
    position:absolute;
    left:9.5rem;
    color:rgb(252, 64, 64);
    /* line-height: 5px; */
    font-size: 10px;
}

img{
    width:40%;
    text-align: center;
}

@media only screen and (max-width: 800px){
    .container{
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: auto;
        box-shadow: 0px 0px 0px 0px rgb(119, 118, 118);
        margin-top: 10px;
        border-radius: 20px;
        /* margin-bottom: 10%; */
        justify-content: center;
        align-items: center
    }
    .container_light{
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: auto;
        border: 1px solid rgb(150, 145, 145);
        margin-top: 10px;
        border-radius: 20px;
        box-shadow: 0px 0px 0px 0px rgb(119, 118, 118);
        /* margin-bottom: 10%; */
        justify-content: center;
        align-items: center;
    }
}
