.image{
    width:30%;
    border-radius: 10px;
    margin-right: 10px;
}
.title{
    font-weight: bold;
}

.container{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 20px;
    flex-direction: flex-row;
    border: 1px solid white;
    padding: 5px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.container_light{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 20px;
    flex-direction: flex-row;
    border: 1px solid grey;
    padding: 5px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.inner_container{
    display: block;
    min-width:100%;
    width:auto
}

.button_grp_container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    width:100%
}

.button_know_more{
    border-radius: 5px;
    background: rgb(55, 136, 55);
    color:white;
    margin:5px;
    box-shadow: 0px 0px 2px 1px white;
    padding:2px;
    cursor:pointer
}

.button_collection{
    border-radius: 5px;
    background: rgb(235, 208, 88);
    color:rgb(48, 2, 2);
    margin:5px;
    box-shadow: 0px 0px 2px 1px white;
    padding:2px;
    font-weight: 600;
    cursor:pointer
}

.button_report{
    border-radius: 5px;
    background: rgb(248, 44, 17);
    color:white;
    margin:5px;
    box-shadow: 0px 0px 2px 1px white;
    padding:2px;
    cursor:pointer
}
@media only screen and (max-width:600px){
    .image{
        width:90%;
        border-radius: 10px;
    }
    
    .container{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
        flex-direction: column;
        border: 1px solid white;
        padding: 5px;
        border-radius: 10px;
        margin-bottom: 20px;
        /* box-shadow: 0px 0px 10px 5px white; */
    }

    .container_light{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
        flex-direction: column;
        border: 1px solid grey;
        padding: 5px;
        border-radius: 10px;
        margin-bottom: 20px;
        /* box-shadow: 0px 0px 10px 5px white; */
    }
    
    .inner_container{
        /* width:100%; */
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        width:auto
    }
    .button_grp_container{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width:100%
    }
}