.head_container {
  display: grid;
  width: 100%;
  color: rgb(12, 236, 236);
  background: rgba(59, 51, 51);
  grid-template-columns: 1fr 2fr;
  grid-template-areas: ".brand .nav_links";

}
.head_container_light {
  display: grid;
  width: 100%;
  color: blue;
  background-image: linear-gradient(to bottom,rgb(255, 209, 139),white,rgb(198, 250, 182));
  grid-template-columns: 1fr 2fr;
  grid-template-areas: ".brand .nav_links";
  /* position: fixed;
  top: 0px; */
}

.brand {
  display: flex;
  align-items: center;
  padding-left: 10px;
  font-size: 2rem;
  padding-bottom: 10px;
  font-weight: 600;
}
.nav_links {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.nav_element {
  color: aqua;
  padding: 10px;
  text-decoration: none;
  cursor: pointer;
}
.nav_element_light {
  color: blue;
  padding: 10px;
  text-decoration: none;
  cursor: pointer;
}

.button {
  border: 1px solid white;
  border-radius: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  width: 48px;
  height: 18px;
}
.button_light {
  flex-direction: row-reverse;
  border: 1px solid black;
}

.label {
  color: azure;
  padding: 3px;
  font-size: 9px;
  cursor: pointer;
}
.label_light {
  color: black;
  padding: 3px;
  font-size: 9px;
  cursor: pointer;
}

.knob {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background: rgb(27, 240, 158);
  padding: 2px;
  cursor: pointer;
}
.knob_light {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background: grey;
  padding: 2px;
  cursor: pointer;
}

.user_avatar{
  height:2rem;
  width:2rem;
  border: 1px solid aqua;
  margin-left: 5px;
  margin-right:5px;
  border-radius: 50%;
}

@media only screen and (max-width: 600px) {
  .head_container {
    position: sticky;
    display: block;
    width: 100%;
    color: rgb(12, 236, 236);
    background: rgba(59, 51, 51);
    height: 100%;
  }
  .head_container_light {
    position: sticky;
    display: block;
    width: 100%;
    height: 100%;
    color: blue;
  background-image: linear-gradient(to bottom,rgb(255, 209, 139),white,rgb(198, 250, 182));
  }
  .brand {
    display: flex;
    align-items: center;
    padding-left: 10px;
    font-size: 2rem;
    padding-bottom: 10px;
    justify-content: center;
    font-weight: 600;
  }
  .nav_links {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}
